:root {
  --bs-blue: #1B70EF;
  --bs-dark: #282C32;
  --bs-gray-50: #eff2ff;
  --bs-gray-100: #F5F5F5;
  --bs-gray-200: #939393;
  --bs-border-color: #E0E0E0;
  --bs-green: #60D66A;
  --bs-shadow-rgb: rgba(0, 0, 0, 0.15);
  --bs-header-shadow: rgba(0, 0, 0, 0.05);
  --bs-font-sans-serif: "Gilroy", "sans-serif";
  --bs-body-font-family: var(--bs-font-sans-serif);
  --bs-font-inter: "Inter", "sans-serif";
  --bs-accordion-bg: rgba(27, 112, 239, 0.04);
}

header {
  box-shadow: 0px 4px 4px 0px var(--bs-header-shadow);
}

.header-top {
  background-color: var(--bs-dark);
  padding-block: 8px;
}

.header-top__links {
  column-gap: 28px;
}
.header-top__links a {
  color: var(--bs-white);
  font-size: 14px;
  font-weight: 400;
  text-decoration: none;
  column-gap: 4px;
}
.header-top__links a img {
  width: 20px;
  flex-shrink: 0;
}

.header-wrapper {
  padding: 8px 0;
}

.header__left {
  column-gap: 19px;
}

.nav__links {
  column-gap: 24px;
}

.logo {
  font-family: var(--bs-font-inter);
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  column-gap: 10px;
}

.header-bottom__btn {
  padding-inline: 15px;
  height: 40px;
  background-color: var(--bs-green);
}

.header-bottom__search {
  padding-inline: 15px;
  background-color: var(--bs-gray-100);
}
.header-bottom__search input {
  height: 40px;
}
.header-bottom__search input::-webkit-search-cancel-button {
  display: none;
}
.header-bottom__search input::placeholder {
  color: var(--bs-dark);
}

.catalog-menu {
  padding: 30px;
  box-shadow: 0px 4px 34px 0px var(--bs-shadow-rgb);
}

.catalog-menu__head {
  gap: 10px;
  grid-template-columns: 160px 200px 170px 240px 170px;
}
.catalog-menu__head .menu__head-item {
  padding: 20px;
  background-color: var(--bs-gray-50);
}
.catalog-menu__head .menu__head-item.active {
  background-color: var(--bs-blue);
}

.menu__head-item h5 {
  font-size: 14px;
}
.menu__head-item:first-child svg {
  transform: translate(0, -5px);
}
.menu__head-item:last-child svg {
  transform: translate(-11px, -13px);
}
.menu__head-item.active svg path {
  fill: var(--bs-white);
}
.menu__head-item.active h5 {
  color: var(--bs-white);
}

.catalog-content {
  grid-template-columns: repeat(2, 1fr);
}
.catalog-content .catalog-content__line {
  padding: 8.5px 0;
}
.catalog-content .catalog-content__line:not(:first-child):not(:nth-child(2)) {
  border-top: 1px solid var(--bs-gray-50);
}
.catalog-content .catalog-content__line h1 {
  font-size: 16px;
}
.catalog-content .catalog-content__line span {
  font-size: 14px;
  background-color: var(--bs-green);
  padding: 6px 10px;
}

.header-md__right {
  display: none;
}

.header-bottom-md {
  background-color: var(--bs-gray-100);
}

.btn-close {
  background: none;
  opacity: 1;
}

.sidebar-acc__btn {
  background-color: var(--bs-gray-50);
  color: var(--bs-dark);
  font-size: 14px;
}
.sidebar-acc__btn svg {
  width: 35.234px;
  height: auto;
}
.sidebar-acc__btn svg path {
  fill: var(--bs-dark);
}
.sidebar-acc__btn[aria-expanded=true] {
  background-color: var(--bs-blue);
  color: var(--bs-white);
}
.sidebar-acc__btn[aria-expanded=true] svg path {
  fill: var(--bs-white);
}

.sidebar {
  overflow-y: scroll;
}
.sidebar .accordion-item {
  border-radius: 10px;
  background: var(--bs-accordion-bg);
}
.sidebar .catalog-content {
  padding-top: 20px;
  padding-bottom: 30px;
}
.sidebar .catalog-content .catalog-content__line {
  padding-inline: 19px;
  padding-block: 10px;
}
.sidebar .catalog-content .catalog-content__line:not(:first-child):not(:nth-child(2)) {
  border: none;
}

@media (max-width: 1125px) {
  header nav,
header .header__left {
    display: none !important;
  }

  .header-md__right {
    display: flex;
  }
}
@media (max-width: 1084px) {
  .catalog-menu__head {
    grid-template-columns: repeat(5, minmax(170px, 1fr));
  }

  .menu__head-item h5 {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
}
@media (max-width: 768px) {
  .logo {
    column-gap: 7px;
    font-size: 11.705px;
  }
  .logo img {
    width: 37.311px;
    height: 39.505px;
  }
}
body {
  font-family: var(--bs-body-font-family);
}

* {
  outline: none;
}

a {
  text-decoration: none;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding-inline: 22px;
}

.breadcrumb {
  column-gap: 4px;
}
.breadcrumb a {
  font-size: 10px;
  padding: 4px 6px;
  background-color: var(--bs-gray-50);
}
.breadcrumb a:first-child {
  color: var(--bs-blue);
}
.breadcrumb a:last-child {
  pointer-events: none;
}

.product-wrapper {
  column-gap: 70px;
  grid-template-columns: 1fr 436px;
}

.product__title {
  font-size: 24px;
}
.product__title span {
  font-size: 16px;
}

.product-grid {
  grid-template-columns: 1fr 1fr;
}

#product_slider .splide__slide {
  height: 265px;
}

#thumbnail_product_slider {
  margin-top: -20px;
}
#thumbnail_product_slider .splide__slide {
  border-radius: 10px;
  border: 1px solid var(--bs-border-color);
  height: 60px;
  border-color: var(--bs-border-color);
  position: relative;
}
#thumbnail_product_slider .splide__slide img {
  width: 100%;
  height: 100%;
  object-fit: scale-down;
}
#thumbnail_product_slider .splide__slide::after {
  content: "";
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0.8) 100%), url(<path-to-image>) lightgray 50%/cover no-repeat;
  opacity: 0.6;
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
}
#thumbnail_product_slider .splide__slide.is-active::after {
  display: none;
}

.param-tags {
  column-gap: 4px;
}
.param-tags li {
  font-size: 12px;
  padding: 6px 10px;
}
.param-tags li.bg-green {
  background-color: var(--bs-green);
}
.param-tags li.bg-blue {
  background-color: var(--bs-blue);
}

.params-line .line * {
  font-size: 12px;
}

.product-params__link {
  font-size: 12px;
  color: var(--bs-green);
}

.order-card {
  background-color: var(--bs-gray-100);
  padding: 20px;
}
.order-card .order-btn {
  height: 40px;
  background-color: var(--bs-dark);
}

.order-header h1 {
  font-size: 26px;
}
.order-header a {
  color: var(--bs-green);
  font-size: 14px;
}

.order-items__title {
  color: var(--bs-gray-200);
  font-size: 10px;
}

.order-items {
  max-height: 300px;
}
.order-items .item {
  padding: 14px 17px;
}
.order-items .item h5 {
  font-size: 16px;
}

.form__inputs input {
  outline: none;
  padding-inline: 20px;
  font-size: 14px;
  width: 100%;
  height: 40px;
  background-color: var(--bs-gray-100);
}
.form__inputs input::placeholder {
  color: var(--bs-dark);
}

.proposal-form__inputs {
  grid-template-columns: 1fr 1fr 1fr;
}

#phone-number {
  column-gap: 6px;
  padding-inline: 20px;
  padding-left: 9px;
  background-color: var(--bs-gray-100);
}
#phone-number .num-code {
  padding: 4px 10px;
  font-size: 14px;
}

.item-checkbox input[type=checkbox] {
  appearance: none;
  width: 23px;
  height: 23px;
  border-radius: 8px;
  border: 1px solid var(--bs-gray-200);
}
.item-checkbox input[type=checkbox]::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: url("../images/check-icon.svg") no-repeat;
  background-position: center center;
  top: 0;
  left: 0;
  display: none;
}
.item-checkbox input[type=checkbox]:checked {
  background-color: var(--bs-dark);
}
.item-checkbox input[type=checkbox]:checked::after {
  display: block;
}
.item-checkbox .item-info b {
  font-size: 14px;
}
.item-checkbox .item-info p {
  font-size: 10px;
}

.proposal-form__policy span {
  font-size: 8px;
}

.policy-check .policy-check__input[type=checkbox] {
  appearance: none;
  width: 23px;
  height: 23px;
  outline: 1px solid var(--bs-dark);
}
.policy-check .policy-check__input[type=checkbox]::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: url("../images/check-icon.svg") no-repeat;
  background-position: center center;
  top: 0;
  left: 0;
  display: none;
}
.policy-check .policy-check__input[type=checkbox]:checked {
  background-color: var(--bs-dark);
}
.policy-check .policy-check__input[type=checkbox]:checked::after {
  display: block;
}
.policy-check .policy-check__label {
  font-size: 14px;
}

.proposal .proposal__title {
  font-size: 24px;
  color: var(--bs-blue);
}
.proposal .proposal-form__btn {
  background-color: var(--bs-green);
  font-size: 14px;
  width: 100%;
  max-width: 300px;
  height: 40px;
}

.product-content-wrapper {
  background-color: var(--bs-gray-100);
  border-radius: 30px;
  padding: 37px 20px 42px 20px;
}

#product-content {
  margin-bottom: 150px;
}

.content-tabs {
  width: fit-content;
  padding: 10px;
  border-radius: 30px;
  column-gap: 4px;
  background-color: var(--bs-white);
  margin-bottom: 67px;
}
.content-tabs .nav-link {
  color: var(--bs-dark);
  border-radius: 30px;
  background: var(--bs-gray-100);
  padding: 10px 30px;
  font-size: 14px;
}
.content-tabs .nav-link span {
  font-weight: 400;
  color: var(--bs-blue);
}
.content-tabs .nav-link.active {
  color: var(--bs-white);
  background-color: var(--bs-dark);
}

.fade:not(.show) {
  display: none;
}

.content1 {
  display: grid;
  column-gap: 42px;
  grid-template-columns: 1fr 370px;
}

.content1__left > *:not(:last-child) {
  margin-bottom: 23px;
}

.fw-800 {
  font-weight: 800;
}

.def-title {
  color: var(--bs-dark);
  font-size: 24px;
  font-weight: 800;
  text-transform: uppercase;
}

.def-txt {
  text-align: justify;
  line-height: normal;
}

.product-content-accordion {
  background-color: var(--bs-white);
  padding: 20px 10px;
  border-radius: 10px;
  margin-bottom: 30px;
}

.product-content-accordion .accordion-button {
  font-size: 14px;
}
.product-content-accordion .accordion-button::after {
  background-image: url(../images/accordion-arrow.svg);
}

.accordion-list li {
  margin-top: 10px;
}
.accordion-list li span {
  font-size: 12px;
}

.content1-right > *:not(:last-child) {
  margin-bottom: 25px;
}
.content1-right .package {
  margin-bottom: 34px;
}

.package {
  border-radius: 10px;
  padding: 24px 25px 30px 25px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 35px;
}
.package .package-item {
  column-gap: 14px;
}

.document {
  padding: 24px 25px 30px 25px;
  border-radius: 10px;
}
.document > *:not(:last-child) {
  margin-bottom: 24px;
}
.document .document-item {
  column-gap: 14px;
}
.document .document-item h5 {
  font-size: 12px;
  margin-bottom: 5px;
}
.document .document-item h6 {
  color: var(--bs-gray-200);
  font-size: 8px;
  font-weight: 800;
}

.montage-flex {
  display: grid;
  column-gap: 38px;
  grid-template-columns: 1fr 370px;
}

.montage-img {
  width: 100%;
}
.montage-img img {
  width: 100%;
  height: auto;
  display: block;
}

#montage {
  margin-bottom: 16px;
}
#montage .splide__slide {
  height: 502px;
}

.swiperMontage2 {
  margin-bottom: 16px;
}

.splide__slide {
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
}

#thumbnail-montage .splide__slide {
  height: 164px;
}

footer {
  padding-block-end: 74px;
  background-color: var(--bs-dark);
}

.footer-top {
  padding: 19px 20px;
  max-width: 1140px;
  box-shadow: 0px 0px 10px 0px var(--bs-shadow-rgb);
}
.footer-top h2 {
  font-size: 24px;
}

.footer-top__form {
  grid-template-columns: 244px 244px 193px;
  column-gap: 4px;
}
.footer-top__form button,
.footer-top__form input {
  background-color: var(--bs-blue);
  padding-inline: 21px;
  height: 40px;
  font-size: 14px;
}
.footer-top__form input {
  color: var(--bs-dark);
  background: var(--bs-gray-100);
}
.footer-top__form input::placeholder {
  color: var(--bs-dark);
}

.footer-wrapper {
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.footer-logo {
  font-size: 16px;
}

.footer-wrapper__serie li {
  font-size: 14px;
}

@media (max-width: 950px) {
  .footer-top {
    align-items: flex-start !important;
    row-gap: 15px;
    flex-direction: column;
  }

  .footer-top__form {
    width: 100%;
    grid-template-columns: 1fr 1fr 1fr;
  }

  .footer-wrapper {
    grid-template-columns: 140px 1fr 1fr 1fr;
  }
}
@media (max-width: 900px) {
  .footer-wrapper {
    grid-template-columns: 200px 1fr 1fr;
    grid-template-rows: repeat(2, auto);
  }
}
@media (max-width: 768px) {
  .footer-top__form {
    row-gap: 4px;
    grid-template-columns: auto;
    grid-template-rows: repeat(1, 1fr);
  }

  footer {
    padding-block-end: 43px;
  }

  .footer-wrapper {
    column-gap: 20px;
    row-gap: 43px;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(1, auto);
  }

  .footer-wrapper {
    margin-top: -40px;
  }
}
@media (max-width: 1200px) {
  .product-wrapper,
.content1,
.montage-flex {
    column-gap: 40px;
  }
}
@media (max-width: 1125px) {
  .product-wrapper,
.montage-flex,
.content1 {
    grid-template-columns: 1fr 350px;
  }

  #montage .splide__slide {
    height: 372px;
  }

  #thumbnail-montage .splide__slide {
    height: 100px;
  }

  .content-tabs {
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    overflow-x: scroll;
  }
  .content-tabs li {
    flex-shrink: 0;
  }
}
@media (max-width: 1020px) {
  .content1 {
    gap: 25px;
    grid-template-columns: 1fr 370px;
    margin-bottom: 26px;
  }

  #montage .splide__slide {
    height: 500px;
  }

  #thumbnail-montage .splide__slide {
    height: 200px;
  }

  #thumbnail_product_slider .splide__slide {
    height: 70px;
  }

  .montage-flex,
.product-wrapper {
    gap: 25px;
    grid-template-columns: auto;
    grid-template-rows: repeat(1, 1fr);
  }

  .product-grid {
    grid-template-columns: 400px 1fr;
  }

  #product_slider .splide__slide {
    height: 300px;
  }

  .product-content-accordion {
    margin: 0;
  }
}
@media (max-width: 950px) {
  .content1 {
    grid-template-columns: 1fr 340px;
  }

  .document .document-item h5 {
    max-width: 100%;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
}
@media (max-width: 900px) {
  .content1 {
    grid-template-columns: auto;
    grid-template-rows: repeat(1, 1fr);
  }

  #thumbnail-montage .splide__slide {
    height: 150px;
  }
}
@media (max-width: 768px) {
  #montage {
    margin-bottom: 6px;
  }

  .product-grid {
    grid-template-columns: auto;
    grid-template-rows: repeat(1, 1fr);
  }

  .product-slide-content {
    max-width: 400px;
    margin: 0 auto;
  }

  .product__title span {
    display: block;
  }

  .proposal-form__inputs {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(1, 1fr);
  }

  .proposal .proposal-form__btn {
    max-width: 100%;
  }
}
@media (max-width: 600px) {
  .product-slide-content {
    max-width: 100%;
  }

  #montage .splide__slide {
    height: 340px;
  }

  #thumbnail-montage .splide__slide {
    height: 100px;
  }

  .order-card,
.product-content-wrapper {
    margin-inline: -22px;
  }
}
@media (max-width: 428px) {
  #montage .splide__slide {
    height: 261px;
  }

  #thumbnail-montage .splide__slide {
    height: 86px;
  }

  #product_slider .splide__slide {
    height: 295.071px;
  }

  #thumbnail_product_slider .splide__slide {
    height: 64.582px;
  }
}